.hero {
  position: relative;
  z-index: 1;
  color: $white;
  @media (max-width: 1279px) {
    overflow: hidden;
  }
  &__media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    video {
      width: 100%;
      @include cover;
    }
  }
  &__content {
    min-height: 100vh;
    min-height: 100svh;
    // height: 915px;
    padding: 136px 0 80px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 40px;
    @media (max-width: 767px) {
      // height: 840px;
      padding: 86px 0 70px;
    }
  }
  &__text {
    text-align: center;
    margin: auto 0;
  }
  &__suptitle {
    @include body-medium;
    margin-bottom: 24px;
  }
  &__title {
    @include h1;
    margin-bottom: 60px;
  }
  &__buttons {
    display: flex;
    justify-content: center;
    gap: $gap;
    @media (max-width: 767px) {
      flex-wrap: wrap;
      gap: 14px;
    }
  }
}