.intro {
  padding-bottom: 80px;
  overflow: hidden;
  position: relative;
  @media (max-width: 1279px) {
    padding-bottom: 50px;
  }
  @media (max-width: 767px) {
    padding-bottom: 40px;
  }
  &__media {
    width: 100%;
    height: 695px;
    @media (max-width: 767px) {
      height: 620px;
    }
    img {
      @include cover;
    }
  }
  &__img {
    position: absolute;
    top: 0;
    // left: 925px;
    left: 48vw;
    width: 916px;
    height: 1215px;
    pointer-events: none;
    @media (max-width: 1279px) {
      width: 700px;
      height: auto;
      top: 25px;
      left: auto;
      right: -200px;
    }
    @media (max-width: 767px) {
      width: auto;
      height: 750px;
      top: 0;
      left: 0;
      right: 0;
      &::before {
        content: '';
        position: absolute;
        bottom: 130px;
        left: 0;
        width: 100%;
        height: 139px;
        background: linear-gradient(180deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 1) 100%);
        pointer-events: none;
      }
      img {
        object-position: right bottom;
      }
    }
    @media (max-width: 374px) {
      &::before {
        bottom: 34vw;
      }
    }
    img {
      @include contain;
    }
  }
  &__content {
    padding-top: 40px;
    position: relative;
    @media (max-width: 767px) {
      padding-top: 31px;
    }
  }
  &__text {
    max-width: 760px;
  }
  &__title {
    @include h1;
    margin-bottom: 16px;
    @media (max-width: 767px) {
      font-size: 38px;
    }
    span {
      display: block;
      color: $blue-mid;
    }
  }
  &__description {
    padding-left: 223px;
    @include h3;
    @media (max-width: 1279px) {
      max-width: 500px;
      padding-left: 90px;
      br {
        display: none;
      }
    }
    @media (max-width: 767px) {
      padding: 0;
    }
  }
}