.list {
  padding-left: 26px;
  li {
    @include body;
    position: relative;
    &::before {
      content: '';
      position: absolute;
      top: 6px;
      left: -20px;
      width: 8px;
      height: 8px;
      background-color: $blue;
      @media (max-width: 767px) {
        top: 4px;
      }
    }
    &:not(:last-child) {
      margin-bottom: 16px;
    }
  }
  &_round {
    padding-left: 52px;
    @media (max-width: 767px) {
      padding-left: 40px;
    }
    li {
      &::before {
        top: 0;
        left: -52px;
        width: 36px;
        height: 36px;
        border-radius: 50%;
        background-color: #AACCFF;
        border: 9px solid #F0F6FF;
        @media (max-width: 767px) {
          top: 0;
          left: -40px;
          width: 24px;
          height: 24px;
          border-width: 6px;
        }
      }
      &:not(:last-child) {
        margin-bottom: 32px;
      }
      span {
        display: block;
        @include h4;
        @media (max-width: 767px) {
          letter-spacing: normal;
        }
        &:not(:last-child) {
          margin-bottom: 12px;
        }
      }
    }
  }
}