.integration {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__title {
    @include h2-bold;
    @include title-img;
    flex-direction: column;
    gap: 30px;
    color: $blue-mid;
    margin-bottom: 40px;
    text-align: center;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
    picture {
      align-self: center;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 44px;
    max-width: 860px;
    margin: 0 auto;
    @media (max-width: 1279px) {
      row-gap: 20px;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
      row-gap: 35px;
      padding: 0 6px;
    }
  }
  &__item {
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    background-color: $white;
    border-radius: 7px;
    text-align: center;
    box-shadow: -25px 30px 82px rgba(#000, .1);
    position: relative;
    @media (max-width: 1279px) {
      padding: 20px 10px;
    }
    @media (max-width: 767px) {
      min-height: 191px;
      padding: 20px 32px;
    }
    &:first-child {
      transform: rotate(-.6deg);
      .pin {
        svg {
          fill: #B5FFBD;
        }
      }
    }
    &:nth-child(2) {
      transform: rotate(.5deg);
      @media (max-width: 767px) {
        min-height: 208px;
      }
    }
    &:nth-child(3) {
      transform: rotate(-1deg);
      @media (max-width: 1279px) {
        transform: rotate(-1deg) translateX(5px)
      }
      @media (max-width: 767px) {
        min-height: 186px;
        transform: rotate(-1deg);
      }
      .pin {
        svg {
          fill: #AACCFF;
        }
      }
    }
    &:nth-child(4) {
      transform: rotate(.9deg);
      background-color: #E2FFE5;
      color: #00780C;
      @media (max-width: 1279px) {
        transform: rotate(.9deg) translateX(-5px)
      }
      @media (max-width: 767px) {
        min-height: 164px;
        transform: rotate(.9deg);
      }
      .pin {
        svg {
          fill: $green;
        }
      }
      .integration__item-title {
        font-weight: 500;
      }
    }
    &-title {
      @include h5;
      &:not(:last-child) {
        margin-bottom: 16px;
      }
    }
    &-brand {
      width: auto;
      height: 40px;
      @media (max-width: 767px) {
        height: 30px;
      }
      &s {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        gap: 12px;
        @media (max-width: 1279px) {
          flex-direction: column;
          justify-content: flex-start;
          gap: 18px;
        }
        @media (max-width: 767px) {
          flex-direction: row;
          justify-content: center;
        }
      }
      img {
        @include contain;
      }
    }
  }
}