.solution {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__title {
    @include h2-bold;
    @include title-img;
    margin-bottom: 40px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    min-height: 696px;
    padding: 30px 30px 500px;
    background-color: #F0F6FF;
    border-radius: 10px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    @media (max-width: 767px) {
      min-height: 478px;
      padding: 16px;
    }
    &:nth-child(1) {
      .solution__item-img {
        @media (max-width: 1279px) {
          img {
            object-fit: cover;
          }
        }
        @media (max-width: 767px) {
          padding-top: 30px;
        }
      }
    }
    &:nth-child(2) {
      background-color: #D5E4FF;
      .solution__item-img {
        @media (max-width: 767px) {
          padding-top: 90px;
        }
      }
    }
    &:nth-child(3) {
      min-height: 301px;
      grid-column: span 2;
      display: flex;
      align-items: center;
      padding: 25px;
      background-color: #EAEAEA;
      @media (max-width: 1279px) {
        display: block;
        padding: 30px;
        grid-column: span 1;
        min-height: 696px;
      }
      @media (max-width: 767px) {
        min-height: 478px;
        padding: 16px;
      }
      .solution {
        &__item {
          &-img {
            img {
              object-position: right;
              @media (max-width: 1279px) {
                object-position: center;
              }
              @media (max-width: 767px) {
                object-position: bottom center;
              }
            }
          }
          &-text {
            max-width: 700px;
            @media (max-width: 1279px) {
              max-width: none;
            }
          }
        }
      }
    }
    &-img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      z-index: -1;
      img {
        @include cover;
        @media (max-width: 1279px) {
          object-fit: contain;
          object-position: bottom;
        }
        @media (max-width: 425px) {
          object-fit: cover;
        }
      }
    }
    &-title {
      @include h4-medium;
      margin-bottom: 14px;
      @media (max-width: 767px) {
        letter-spacing: normal;
        margin-bottom: 12px;
      }
    }
    &-description {
      @include h5;
      @media (max-width: 1279px) {
        max-width: 620px;
        br {
          display: none;
        }
      }
    }
  }
}