.slider {
  &-button {
    &s {
      display: flex;
      align-items: center;
      gap: 20px;
      @media (max-width: 767px) {
        display: none;
      }
      .slider {
        &-button {
          &-prev, &-next {
            position: static;
            transform: none;
          }
        }
      }
    }
    &-prev, &-next {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 46px;
      height: 46px;
      transform: translateY(-50%);
      outline: none;
      margin: 0;
      z-index: 10;
      transition: all $transition;
      // &.swiper-button-disabled {
      //   opacity: 1;
      //   svg {
      //     fill: $almost-black-40;
      //   }
      // }
      &:hover {
        svg {
          fill: $blue-dark-active;
        }
      }
      &:active {
        svg {
          transform: scale(.8);
        }
      }
      &::after {
        content: none;
      }
      svg {
        width: 100%;
        height: 100%;
        fill: $blue-dark;
        transition: all $transition;
      }
    }
    &-prev {
      left: 0;
    }
    &-next {
      right: 0;
    }
  }
  &-pagination.swiper-pagination {
    position: static;
    display: flex;
    justify-content: center;
    gap: 6px;
    margin-top: 20px;
    &.swiper-pagination-lock {
      display: none;
    }
    .swiper-pagination-bullet {
      width: 32px;
      height: 32px;
      background: none;
      border-radius: 0;
      margin: 0;
      opacity: 1;
      position: relative;
      transition: all $transition;
      svg {
        width: 100%;
        height: 100%;
        fill: $grey;
        path {
          transition: all $transition;
        }
        .inner {
          fill: transparent;
          stroke: transparent;
        }
      }
      &-active {
        svg {
          fill: $red;
          .inner {
            fill: $red;
            stroke: $red;
          }
        }
      }
    }
  }
}