.loyalty {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 45px $gap;
    @media (max-width: 1279px) {
      padding-top: 20px;
      display: block;
    }
    @media (max-width: 767px) {
      padding-top: 0;
    }
  }
  &__title {
    grid-column: span 2;
    @include h2-bold;
    @media (max-width: 1279px) {
      margin-bottom: 11px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__img {
    grid-row: 1 / span 2;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    @media (max-width: 1279px) {
      height: 388px;
      margin-bottom: 16px;
    }
    @media (max-width: 1023px) {
      height: 288px;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 1279px) {
        position: static;
      }
    }
    img {
      @include cover;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: #F9F9F9;
    border-radius: 10px;
    @media (max-width: 1279px) {
      padding: 16px;
      justify-content: center;
    }
    &s {
      grid-column: span 2;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &:last-child {
      justify-content: center;
      background-color: #E2FFE5;
      color: #00780C;
    }
    &-title {
      @include h4;
      margin-bottom: 13px;
      @media (max-width: 767px) {
        letter-spacing: normal;
      }
    }
    &-subtitle {
      @include h5-medium;
      text-align: center;
    }
    &-description {
      @include body;
    }
  }
}