.reviews {
  padding: 160px 0;
  overflow: hidden;
  @media (max-width: 1279px) {
    padding: 100px 0;
  }
  @media (max-width: 767px) {
    padding: 60px 0;
  }
  &__heading {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    gap: $gap;
    margin-bottom: 60px;
    @media (max-width: 1279px) {
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__title {
    max-width: 800px;
    @include h2;
  }
  &__slider {
    overflow: visible;
    &-item {
      height: auto;
      min-height: 427px;
      display: flex;
      flex-direction: column;
      padding: 30px;
      background-color: $blue-light;
      transition: background-color $transition;
      @media (max-width: 767px) {
        width: 269px;
        min-height: 350px;
        padding: 16px;
      }
      &.swiper-slide-active {
        background-color: $blue-dark;
        color: $white;
      }
    }
    &-description {
      @include h5;
      margin-bottom: 24px;
      transition: all $transition;
    }
    &-author {
      margin-top: auto;
      display: flex;
      align-items: center;
      gap: 18px;
      &-photo {
        width: 46px;
        height: 46px;
        border-radius: 50%;
        overflow: hidden;
        img {
          @include cover;
        }
      }
      &-text {
        @include body;
        transition: all $transition;
      }
      &-name {
        margin-bottom: 3px;
      }
    }
  }
}