.feedback {
  padding: 60px 0;
  background-color: $dark;
  color: $white;
  @media (max-width: 1279px) {
    padding: 60px 0 40px;
  }
  @media (max-width: 767px) {
    padding: 30px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    align-items: center;
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
      gap: 30px $gap;
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);

    }
  }
  &__title {
    grid-column: span 3;
    @include h2;
    @media (max-width: 1279px) {
      grid-column: span 2;
    }
    @media (max-width: 767px) {
      grid-column: span 1;
    }
  }
  &__button.button {
    width: 100%;
  }
}