.ticker {
  width: 100%;
  @media (max-width: 1279px) {
    overflow: visible;
  }
  &-wrapper {
    transition-timing-function: linear;
  }
  &__item {
    width: auto;
    display: flex;
    align-items: center;
    &:not(:last-child) {
      margin-right: 46px;
    }
  }
  &__elem {
    flex-shrink: 0;
    width: auto;
    height: 54px;
    &:not(:last-child) {
      margin-right: 46px;
    }
    img {
      // @include contain;
      width: auto;
      height: 100%;
    }
  }
}