.form {
  &__content {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 60px;
    @media (max-width: 767px) {
      gap: 40px;
    }
  }
  &__title {
    @include h3;
    color: $white;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      margin-bottom: 16px;
    }
  }
  &__block {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1023px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
    &_row {
      grid-template-columns: repeat(1, 1fr);
      @media (max-width: 1023px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }
  &__bottom {
    margin-top: 60px;
    @media (max-width: 767px) {
      margin-top: 40px;
    }
  }
  &__button.button {
    width: 100%;
    &::before {
      content: none;
    }
    &:hover {
      background-color: $white;
    }
    &:active {
      background-color: $grey-light;
      color: $dark;
      svg {
        fill: $dark;
      }
    }
    &.button_green {
      &:hover {
        background-color: $green;
        color: $white;
      }
      &:active {
        background-color: $green;
        color: $white;
      }
    }
  }
}

.label {
  display: flex;
  cursor: pointer;
  position: relative;
  z-index: 1;
  &_wide {
    grid-column: span 3;
    @media (max-width: 1023px) {
      grid-column: span 2;
    }
    @media (max-width: 767px) {
      grid-column: span 1;
    }
    &_table {
      @media (max-width: 1023px) {
        grid-column: span 2;
      }
      @media (max-width: 767px) {
        grid-column: span 1;
      }
    }
  }
}

input, textarea {
  width: 100%;
  height: 60px;
  border: 1px solid transparent;
  border-radius: 0;
  background-color: $input;
  padding: 19px;
  appearance: none;
  @include body-medium;
  color: $white;
  transition: all $transition;
  outline: none;
  cursor: pointer;
  @media (max-width: 767px) {
    // @include h5-medium;
    font-size: 20px;
  }
  &:hover {
    background-color: $input-light;
  }
  &:focus, &:active {
    outline: none;
    border-color: $blue-dark;
  }
  &:checked {
    ~ .label {
      &__box {
        background-color: $white;
        &::after {
          opacity: 1;
        }
      }
      &__text {
        background-color: $blue-dark;
        span {
          color: $white;
        }
      }
    }
  }
  &::placeholder {
    color: $grey;
  }
  &.visually-hidden {
    position: absolute;
    width: 1px;
    height: 1px;
    margin: -1px;
    border: 0;
    padding: 0;
    clip: rect(0 0 0 0);
    overflow: hidden;
  }
}

textarea {
  height: 120px;
  resize: none;
}

.label {
  &_checkbox {
    -webkit-tap-highlight-color: transparent;
    &:hover {
      input:not(:checked) ~ .label {
        &__box {
          background-color: $input;
        }
        &__text {
          background-color: $input-light;
        }
      }
    }
  }
  &__box {
    position: absolute;
    top: 11px;
    right: 11px;
    width: 22px;
    height: 22px;
    border-radius: 50%;
    background-color: $input-light;
    overflow: hidden;
    transition: all $transition;
    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      width: 14px;
      height: 14px;
      background: url(../img/content/check.svg) 50% / contain no-repeat;
      opacity: 0;
      transition: all $transition;
    }
  }
  &__text {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 120px;
    padding: 16px 30px;
    background-color: $input;
    @include body-medium;
    color: $white;
    transition: all $transition;
    @media (max-width: 767px) {
      height: auto;
      min-height: 60px;
      justify-content: flex-start;
      padding: 16px 19px;
      // @include h5;
      font-size: 20px;
      line-height: 1;
    }
    span {
      @include body-medium-small;
      color: $grey;
      transition: all $transition;
      @media (max-width: 767px) {
        @include body-medium;
      }
    }
    &:has(span) {
      flex-direction: column;
      align-items: flex-start;
      gap: 10px;
      @media (max-width: 767px) {
        min-height: 96px;
        justify-content: center;
        gap: 6px;
      }
    }
  }
}