.services-all {
  .container {
    @media (max-width: 1279px) {
      padding: 0;
    }
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__side {
    position: sticky;
    top: 88px;
    left: 0;
    // height: 989px;
    height: calc(100vh - 87px);
    grid-column: span 5;
    padding: 30px 90px;
    background-color: $blue-light;
    display: flex;
    align-items: center;
    @media (max-width: 1279px) {
      display: none;
    }
    &-list {
      li {
        &:not(:last-child) {
          margin-bottom: 40px;
        }
      }
    }
    &-link {
      @include h3;
      transition: all $transition;
      &:hover {
        color: $blue-dark;
      }
      &:active {
        color: $blue-dark-active;
      }
      &_active {
        color: $blue-dark;
      }
    }
  }
  &__content {
    grid-column: span 7;
    margin-left: -16px;
    @media (max-width: 1279px) {
      margin-left: 0;
    }
  }
}