.footer {
  padding: 60px 0;
  background-color: $dark;
  color: $white;
  overflow: hidden;
  @media (max-width: 1279px) {
    padding: 40px 0 60px;
  }
  @media (max-width: 767px) {
    padding: 30px 0;
  }
  &__info {
    display: flex;
    justify-content: space-between;
    gap: $gap;
    margin-bottom: 60px;
    @media (max-width: 767px) {
      display: block;
      margin-bottom: 30px;
    }
    &-item {
      @media (max-width: 767px) {
        &:not(:last-child) {
          margin-bottom: 16px;
        }
      }
    }
    &-title {
      @include h5;
      margin-bottom: 12px;
      @media (max-width: 767px) {
        margin-bottom: 10px;
      }
    }
    &-link {
      @include h3;
      transition: all $transition;
      &:hover {
        color: $blue-dark;
      }
      &:active {
        color: $blue-dark-active;
      }
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
    gap: 60px;
    @include body;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: flex-start;
      gap: $gap;
    }
    &-link {
      color: $grey;
      transition: all $transition;
      &:hover {
        color: $blue-dark;
      }
      &:active {
        color: $blue-dark-active;
      }
    }
  }
}