.not-found {
  position: relative;
  z-index: 1;
  overflow: hidden;
  &__media {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    video {
      width: 100%;
      @include cover;
    }
  }
  &__img {
    position: absolute;
    bottom: -95px;
    left: 50%;
    transform: translateX(-50%);
    z-index: -1;
    max-width: 92%;
    width: 1294px;
    height: auto;
    @media (max-width: 1279px) {
      bottom: 0;
    }
    @media (max-width: 767px) {
      max-width: 100%;
    }
    img {
      @include contain;
    }
  }
  &__content {
    min-height: 100vh;
    min-height: 100svh;
    padding: 180px 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    color: $white;
    @media (max-width: 1279px) {
      padding: 190px 0;
    }
  }
  &__title {
    @include h1;
    margin-bottom: 24px;
  }
  &__subtitle {
    @include h5-medium;
    margin-bottom: 30px;
  }
}