.request {
  padding: 168px 0 20px;
  background-color: $dark;
  @media (max-width: 767px) {
    padding: 110px 0 10px;
  }
  .container {
    max-width: 1218px;
  }
  &__heading {
    color: $white;
    margin-bottom: 60px;
    @media (max-width: 767px) {
      margin-bottom: 40px;
    }
  }
  &__title {
    display: flex;
    align-items: center;
    gap: 20px;
    @include h2;
    margin-bottom: 20px;
    @media (max-width: 767px) {
      gap: 8px;
    }
    svg {
      width: 40px;
      height: 40px;
      @media (max-width: 767px) {
        width: 24px;
        height: 24px;
      }
    }
  }
  &__description {
    max-width: 600px;
    @include h5;
  }
}