.decor {
  display: inline-block;
  margin-right: -6px;
  padding: 10px 23px;
  background-color: #F0F6FF;
  border-radius: 6px;
  color: $blue-mid;
  position: relative;
  @media (max-width: 1279px) {
    padding: 6px 16px;
    margin-right: 0;
  }
  @media (max-width: 767px) {
    padding: 6px 26px;
  }
  &::before, &::after {
    content: '';
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    width: 5px;
    height: 135%;
    background-color: $blue-mid;
    border-radius: 10px;
    @media (max-width: 1279px) {
      height: 140%;
    }
    @media (max-width: 767px) {
      height: 120%;
    }
  }
  &::after {
    left: auto;
    right: 0;
  }
  span {
    position: absolute;
    top: -24px;
    left: 2px;
    transform: translateX(-50%);
    display: block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    background-color: $blue-mid;
    @media (max-width: 767px) {
      top: -20px;
    }
  }
}