.button {
  width: 318px;
  height: 57px;
  padding: 5px $gap;
  background-color: $blue-dark;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  @include button-small;
  color: $white;
  transition: all $transition;
  position: relative;
  overflow: hidden;
  z-index: 1;
  @media (max-width: 1279px) {
    width: 255px;
  }
  @media (max-width: 767px) {
    width: 100%;
  }
  &::before {
    content: '';
    position: absolute;
    top: 100%;
    right: 100%;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    background-color: $white;
    transition: all .4s;
    z-index: -1;
    pointer-events: none;
  }
  &:hover {
    color: $dark;
    svg {
      fill: $dark;
    }
    &::before {
      transform: scale(10);
    }
  }
  &:active {
    &::before {
      background-color: $grey-light;
    }
  }
  svg {
    width: 24px;
    height: 24px;
    fill: $white;
    transition: all $transition;
  }
  &_hover_dark {
    &:hover {
      background-color: $dark;
      color: $white;
      svg {
        fill: $white;
      }
    }
    &:active {
      background-color: $dark;
    }
  }
  &_border {
    background-color: transparent;
    border: 2px solid $blue-dark;
    color: $blue-dark;
    &::before {
      background-color: $blue-dark;
    }
    &:hover {
      color: $white;
    }
    &:active {
      border-color: $blue-dark-active;
      &::before {
        background-color: $blue-dark-active;
      }
    }
    &_white {
      border-color: $white;
      color: $white;
      &::before {
        background-color: $white;
      }
      &:hover {
        color: $dark;
      }
      &:active {
        border-color: $grey-light;
        &::before {
          background-color: $grey-light;
        }
      }
    }
  }
  &_white {
    background-color: $white;
    color: $dark;
    &::before {
      background-color: $dark;
    }
    &:hover {
      color: $white;
    }
    &:active {
      color: $white;
      &::before {
        background-color: $dark;
      }
    }
  }
  &_dark {
    background-color: $dark;
    &::before {
      background-color: $blue-dark;
    }
    &:hover {
      color: $white;
      svg {
        fill: $white;
      }
    }
    &:active {
      &::before {
        background-color: $blue-dark-active;
      }
    }
  }
  &_green {
    background-color: $green;
  }
}