.advantages {
  @include section;
  &__title {
    max-width: 650px;
    @include h2;
    margin-bottom: 60px;
    @media (max-width: 1279px) {
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 1023px) {
      grid-template-columns: repeat(1, 1fr);
    }
    @media (max-width: 767px) {
      gap: 12px;
    }
  }
  &__item {
    min-height: 343px;
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: $blue-light;
    @media (max-width: 1279px) {
      min-height: auto;
    }
    @media (max-width: 767px) {
      padding: 16px;
    }
    &-img {
      width: 32px;
      height: 32px;
      margin-bottom: 24px;
      svg {
        width: 100%;
        height: 100%;
      }
    }
    &-title {
      @include h3;
      margin-bottom: 24px;
      @media (max-width: 1279px) {
        margin-bottom: 16px;
      }
    }
    &-description {
      margin-top: auto;
      @include body;
      color: $grey;
    }
  }
}