.steps {
  @include section;
  height: 250vh;
  @media (max-width: 767px) {
    height: 250svh;
  }
  &__inner {
    position: sticky;
    top: 140px;
    padding-bottom: 200px;
    // height: calc(100vh - 140px);
    // height: 620px;
    @media (max-width: 1279px) {
      padding-bottom: 210px;
    }
    @media (max-width: 767px) {
      padding-bottom: 190px;
    }
  }
  &__line {
    position: absolute;
    top: 180px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: $grey-light;
    z-index: -1;
    @media (max-width: 1279px) {
      top: 140px;
    }
    @media (max-width: 767px) {
      top: 115px;
    }
    &:nth-child(2) {
      top: 310px;
      @media (max-width: 1279px) {
        top: 270px;
      }
      @media (max-width: 767px) {
        top: 245px;
      }
    }
    &:nth-child(3) {
      top: 440px;
      @media (max-width: 1279px) {
        top: 400px;
      }
      @media (max-width: 767px) {
        top: 375px;
      }
    }
    &:nth-child(4) {
      top: 570px;
      @media (max-width: 1279px) {
        top: 530px;
      }
      @media (max-width: 767px) {
        top: 505px;
      }
    }
  }
  &__title {
    @include h2;
    margin-bottom: 60px;
    @media (max-width: 1279px) {
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__progress {
    display: flex;
    justify-content: space-between;
    margin-bottom: 64px;
    position: relative;
    &-item {
      @include body;
      color: $grey;
    }
  }
  &__range {
    position: absolute;
    top: 62px;
    left: 1%;
    right: 8px;
    transform: translateX(-100%);
    height: 390px;
    width: 3px;
    background-color: $blue-dark;
    transition: all $transition;
    @media (max-width: 767px) {
      top: 57px;
    }
    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      transform: translate(-50%, -50%);
      width: 26px;
      height: 26px;
      border-radius: 50%;
      background-color: $blue-dark;
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    @media (max-width: 1279px) {
      position: relative;
    }
    @media (max-width: 767px) {
      display: block;
    }
  }
  &__item {
    width: 429px;
    padding: 30px;
    background-color: $grey-light;
    position: relative;
    @media (max-width: 1279px) {
      width: 437px;
    }
    @media (max-width: 767px) {
      width: 100%;
      padding: 16px;
    }
    &:not(:first-child) {
      margin-left: -132px;
      @media (max-width: 1279px) {
        margin-left: 0;
        position: absolute;
      }
    }
    &:nth-child(2) {
      background-color: $blue-dark;
      color: $white;
      @media (max-width: 1279px) {
        left: calc(41% - 200px);
      }
      @media (max-width: 767px) {
        left: 0;
      }
      .steps__item {
        &-info, &-description {
          color: $white;
        }
      }
    }
    &:nth-child(3) {
      background-color: $dark;
      color: $white;
      @media (max-width: 1279px) {
        right: calc(41% - 200px);
      }
      @media (max-width: 767px) {
        right: 0;
      }
      .steps__item {
        &-info, &-description {
          color: $white;
        }
      }
    }
    &:nth-child(4) {
      background-color: $blue-light;
      @media (max-width: 1279px) {
        right: 0;
      }
    }
    &-heading {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: $gap;
      @include h5-medium;
      margin-bottom: 32px;
    }
    &-info {
      color: $blue-dark;
    }
    &-description {
      @include body-medium-small;
      color: $grey;
      @media (max-width: 767px) {
        font-size: 17px;
      }
    }
  }
}