.nav {
  &__item {
    &:not(:last-child) {
      margin-bottom: 66px;
      @media (max-width: 1279px) {
        margin-bottom: 62px;
      }
      @media (max-width: 767px) {
        margin-bottom: 30px;
      }
    }
  }
  &__link {
    display: inline-flex;
    align-items: center;
    gap: 20px;
    @include h4-medium;
    color: $white;
    transition: all $transition;
    @media (max-width: 374px) {
      gap: $gap;
    }
    &:hover {
      color: $blue-dark;
      svg {
        fill: $blue-dark;
      }
    }
    &:active {
      color: $blue-dark-active;
      svg {
        fill: $blue-dark-active;
      }
    }
    svg {
      width: 46px;
      height: 46px;
      flex-shrink: 0;
      fill: $blue;
      transition: all $transition;
      @media (max-width: 374px) {
        width: 32px;
        height: 32px;
      }
    }
  }
}