.offer {
  &__block {
    @include section;
    @media (max-width: 767px) {
      padding: 40px 0;
    }
    &:nth-child(even) {
      .offer {
        &__list {
          li {
            &::before {
              background-color: #89E875;
              border-color: #DBFFE0;
            }
          }
        }
        &__description {
          background-color: #E2FFE5;
          color: $green;
        }
      }
    }
  }
  &__title {
    @include h2-bold;
    margin-bottom: 40px;
    // @media (max-width: 1279px) {
    //   br {
    //     display: none;
    //   }
    // }
    @media (max-width: 767px) {
      margin-bottom: 30px;
      span {
        display: block;
        margin-top: 6px;
        font-size: 20px;
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__img {
    grid-column: span 5;
    width: 100%;
    height: 531px;
    @media (max-width: 1279px) {
      margin-bottom: 40px;
    }
    @media (max-width: 767px) {
      height: auto;
      margin-bottom: 30px;
    }
    img {
      @include contain;
    }
  }
  &__text {
    grid-column: 7 / span 6;
  }
  &__list {
    &:not(:last-child) {
      margin-bottom: 32px;
      @media (max-width: 1279px) {
        margin-bottom: 24px;
      }
      @media (max-width: 767px) {
        margin-bottom: 30px;
      }
    }
  }
  &__description {
    clip-path: polygon(0 0, 100% 0, 92% 50%, 100% 100%, 0 100%, 8% 50%);
    padding: 24px 60px;
    background-color: #F0F6FF;
    @include body-medium;
    color: $blue-dark;
    text-align: center;
    @media (max-width: 1279px) {
      max-width: 651px;
      margin: 0 auto;
    }
    @media (max-width: 767px) {
      min-height: 74px;
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 10px 50px;
      br {
        display: none;
      }
    }
    @media (max-width: 424px) {
      padding: 10px 40px;
    }
  }
}