.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 500;
  // background-color: $white;
  transition: all $transition, padding-right 0s;
  pointer-events: none;
  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: $dark;
    opacity: 0;
    pointer-events: none;
    z-index: -1;
  }
  &_active {
    pointer-events: all;
    background-color: $dark;
    &::before {
      opacity: 1;
    }
    .header__body {
      // background-color: $dark;
    }
  }
  &_scroll {
    background-color: $blue-dark;
    pointer-events: all;
  }
  &__body {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 20px 0;
    transition: all $transition;
  }
  .logo {
    display: inline-flex;
    width: 88px;
    height: 38px;
    flex-shrink: 0;
    pointer-events: all;
    // svg {
    //   width: 100%;
    //   height: 100%;
    // }
  }
  &__content {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;
    height: 100dvh;
    margin: 0;
    z-index: -2;
    transform: translateY(-150%);
    transition: all .4s ease;
    // padding: 82px 25px 30px;
    background-color: $dark;
    overflow-x: hidden;
    overflow-y: auto;
    &_active {
      transform: translateY(0%);
    }
    .container {
      height: 100%;
    }
    &-inner {
      display: flex;
      flex-direction: column;
      gap: 40px;
      min-height: 100%;
      padding: 100px 0 40px;
    }
    &-group {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      margin: auto 0;
      @media (max-width: 1279px) {
        grid-template-columns: repeat(8, 1fr);
        .nav {
          grid-column: span 5;
        }
      }
      @media (max-width: 767px) {
        display: flex;
        flex-direction: column;
        gap: 60px;
        // flex-grow: 1;
        // justify-content: space-between;
      }
    }
  }
  &__info {
    color: $white;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    gap: $gap;
    @media (max-width: 1279px) {
      grid-column: span 3;
    }
    &-title {
      @include h5-medium;
      margin-bottom: 12px;
    }
    &-link {
      @include h3;
      transition: all $transition;
      &:hover {
        color: $blue-dark;
      }
      &:active {
        color: $blue-dark-active;
      }
    }
  }
  &__block {
    display: flex;
    align-items: center;
    gap: 60px;
    @include body;
    color: $white;
    @media (max-width: 767px) {
      flex-direction: column;
      align-items: flex-start;
      gap: $gap;
    }
  }
  &__privacy {
    color: $grey;
    transition: all $transition;
    &:hover {
      color: $blue-dark;
    }
    &:active {
      color: $blue-dark-active;
    }
  }
}

.header-animate {
  transition: all $transition;
  animation-duration: .4s;
  animation-timing-function: linear;
  animation-fill-mode: forwards;
  opacity: 0;
  transform: translate3d(0, 100%, 0)
}

.header-animate_up {
  -webkit-animation-name: headerAnimateUp;
  animation-name: headerAnimateUp;
}
.header-animate_down {
  -webkit-animation-name: headerAnimateDown;
  animation-name: headerAnimateDown;
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

@keyframes headerAnimateUp {
  from {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
  to {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}

@keyframes headerAnimateDown {
  from {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
  to {
    opacity: 0;
    transform: translate3d(0, 100%, 0);
  }
}