.portfolio-card {
  &__img {
    width: 100%;
    height: 455px;
    margin-bottom: 10px;
    @media (max-width: 767px) {
      // height: 220px;
      height: auto;
      aspect-ratio: 16 / 10;
    }
    img {
      @include cover;
    }
  }
  &__title {
    @include h4;
    color: $blue-dark;
    margin-bottom: 16px;
  }
  &__label {
    height: 32px;
    display: flex;
    align-items: center;
    padding: 5px 18px 2px;
    background-color: $blue-light;
    @include body-medium-small;
    line-height: 1;
    @media (max-width: 767px) {
      height: 28px;
    }
    &s {
      display: flex;
      flex-wrap: wrap;
      gap: 8px 12px;
      margin-bottom: 16px;
    }
  }
  &__description {
    @include body;
    color: $grey;
  }
}