.portfolio {
  padding: 80px 0 160px;
  @media (max-width: 1279px) {
    padding: 50px 0 100px;
    overflow: hidden;
  }
  @media (max-width: 767px) {
    padding: 30px 0 60px;
  }
  &__title {
    @include h2;
    margin-bottom: 40px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__filter {
    margin-bottom: 40px;
    @media (max-width: 1279px) {
      display: flex;
      margin-left: -30px;
      margin-right: -30px;
      margin-bottom: 30px;
      overflow-x: auto;
      -ms-overflow-style: none;
      scrollbar-width: none;
      &::-webkit-scrollbar {
        width: 0;
        height: 0;
      }
    }
    @media (max-width: 767px) {
      margin-left: -16px;
      margin-right: -16px;
      margin-bottom: 16px;
    }
    &-inner {
      display: flex;
      gap: $gap;
      @media (max-width: 1279px) {
        gap: 12px;
        flex-shrink: 0;
      }
    }
    &-item {
      flex-shrink: 0;
      height: 40px;
      display: flex;
      align-items: center;
      padding: 5px 25px 2px;
      background-color: $grey-light;
      @include body;
      transition: all $transition;
      @media (max-width: 1279px) {
        &:first-child {
          margin-left: 30px;
        }
        &:last-child {
          margin-right: 30px;
        }
      }
      @media (max-width: 767px) {
        height: 37px;
        &:first-child {
          margin-left: 16px;
        }
        &:last-child {
          margin-right: 16px;
        }
      }
      &:hover {
        background-color: $blue-dark;
        color: $white;
      }
      &:active {
        background-color: $blue-dark-active;
      }
      &_active {
        background-color: $blue-dark;
        color: $white;
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 60px $gap;
    @media (max-width: 1023px) {
      grid-template-columns: repeat(1, 1fr);
      gap: 30px;
    }
    @media (max-width: 767px) {
      gap: $gap;
    }
  }
  &__button {
    &s {
      margin-top: 60px;
      display: flex;
      justify-content: center;
      gap: $gap;
      @media (max-width: 1279px) {
        margin-top: 40px;
      }
      @media (max-width: 767px) {
        margin-top: 30px;
        flex-wrap: wrap;
      }
    }
  }
}