.problem {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__title {
    @include h2-bold;
    @include title-img;
    margin-bottom: 40px;
    @media (max-width: 767px) {
      flex-direction: column;
      gap: 12px;
      margin-bottom: 30px;
      picture {
        align-self: center;
      }
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: flex-end;
    @media (max-width: 1279px) {
      display: block;
    }
    // @media (max-width: 1279px) {
    //   grid-template-columns: repeat(2, 1fr);
    // }
    // @media (max-width: 1023px) {
    //   grid-template-columns: repeat(1, 1fr);
    // }
    // @media (max-width: 767px) {
    //   gap: 12px;
    // }
  }
  &__item {
    min-height: 446px;
    padding: 60px 24px;
    background-color: #F9F9F9;
    display: flex;
    flex-direction: column;
    gap: 32px;
    @media (max-width: 1279px) {
      min-height: auto;
      padding: 40px 24px;
    }
    @media (max-width: 767px) {
      padding: 16px;
    }
    &:nth-child(2) {
      min-height: 512px;
      background-color: #F0F6FF;
      @media (max-width: 1279px) {
        min-height: auto;
      }
    }
    &:nth-child(3) {
      min-height: 579px;
      background-color: #E2ECFF;
      @media (max-width: 1279px) {
        min-height: auto;
      }
    }
    &-number {
      @include h4-medium;
      color: rgba(#373737, .22);
      @media (max-width: 1279px) {
        margin-bottom: -8px;
      }
    }
    &-title {
      margin-top: auto;
      @include h4-medium;
      @media (max-width: 767px) {
        letter-spacing: normal;
      }
    }
    &-list {
      padding-left: 34px;
      li {
        @include body;
        position: relative;
        &.exclamation {
          &::before {
            background-image: url(../img/content/list-icon-exclamation.png);
          }
        }
        &::before {
          content: '';
          position: absolute;
          top: 0;
          left: -34px;
          width: 22px;
          height: 22px;
          background: url(../img/content/list-icon.png) 50% / contain no-repeat;
        }
        &:not(:last-child) {
          margin-bottom: 10px;
          @media (max-width: 767px) {
            margin-bottom: 13px;
          }
        }
      }
    }
  }
}