.burger {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  flex-shrink: 0;
  padding: 5px;
  background-color: $white;
  position: relative;
  z-index: 1;
  cursor: pointer;
  pointer-events: all;
  @media (max-width: 767px) {
    width: 38px;
    height: 38px;
  }
  span {
    width: 22px;
    height: 3px;
    background-color: $blue-dark;
    transition: all $transition;
    @media (max-width: 767px) {
      width: 18px;
    }
    &:not(:last-child) {
      margin-bottom: 4px;
    }
    &:last-child {
      background-color: $blue;
    }
  }
  &_active {
    span {
      margin: 0;
      &:nth-child(1) {
        transform: rotate(45deg);
        margin-bottom: -3px;
      }
      &:nth-child(2) {
        display: none;
      }
      &:nth-child(3) {
        transform: rotate(-45deg);
      }
    }
  }
}