.discuss {
  @include section;
  padding-bottom: 138px;
  position: relative;
  overflow: hidden;
  @media (max-width: 1279px) {
    padding-bottom: 625px;
  }
  @media (max-width: 767px) {
    padding: 40px 0 365px;
  }
  &__heading {
    text-align: center;
    margin-bottom: 170px;
    @media (max-width: 1279px) {
      margin-bottom: 50px;
    }
    @media (max-width: 767px) {
      margin-bottom: 27px;
    }
  }
  &__suptitle {
    @include h5;
    margin-bottom: 14px;
    @media (max-width: 1279px) {
      font-weight: 500;
    }
  }
  &__title {
    max-width: 800px;
    margin: 0 auto;
    @include h2-bold;
    @media (max-width: 767px) {
      font-size: 21px;
    }
    .decor {
      margin: -4px 0 0;
      padding: 5px 23px;
      @media (max-width: 1279px) {
        margin: 0;
        padding: 2px 28px;
      }
      @media (max-width: 767px) {
        padding: 2px 8px;
      }
      &::before, &::after {
        height: 110%;
        @media (max-width: 767px) {
          width: 2px;
        }
      }
      span {
        top: -3px;
        @media (max-width: 1279px) {
          width: 16px;
          height: 16px;
        }
        @media (max-width: 767px) {
          left: 1px;
          width: 9px;
          height: 9px;
        }
      }
    }
  }
  &__content {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    @media (max-width: 1279px) {
      justify-content: center;
    }
  }
  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 38vw;
    height: 574px;
    @media (max-width: 1440px) {
      right: 34vw;
    }
    @media (max-width: 1279px) {
      max-width: 1189px;
      right: -35px;
    }
    @media (max-width: 767px) {
      right: 0;
      height: 335px;
      img {
        object-fit: contain;
        object-position: bottom;
      }
    }
    img {
      object-position: left bottom;
      @include contain;
      @media (max-width: 1920px) {
        object-position: right;
        @include cover;
      }
      @media (max-width: 700px) {
        object-fit: cover;
      }
    }
  }
  &__card {
    width: 100%;
    max-width: 409px;
    min-height: 300px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px;
    background-color: $white;
    border-radius: 7px;
    text-align: center;
    box-shadow: -25px 30px 82px rgba(#000, .1);
    transform: rotate(.5deg);
    position: relative;
    @media (max-width: 767px) {
      min-height: 293px;
      padding: 20px 12px;
    }
    &-title {
      max-width: 260px;
      margin: 0 auto 20px;
      @include h5;
    }
    &-info {
      transform: rotate(.5deg);
      @include h4;
    }
    &-name {
      font-weight: 500;
      margin-bottom: 11px;
    }
    &-phone {
      display: inline-block;
      color: $blue-mid;
      margin-bottom: 12px;
    }
    &-link {
      display: inline-flex;
      align-items: center;
      gap: 8px;
      svg {
        width: 32px;
        height: 32px;
        flex-shrink: 0;
      }
    }
  }
}