.analytics {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: $gap;
    align-items: flex-end;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    @include h2-bold;
    @include title-img;
    flex-direction: column;
    gap: $gap;
    margin-bottom: 40px;
    @media (max-width: 1279px) {
      flex-direction: row;
    }
    @media (max-width: 767px) {
      flex-direction: column;
      text-align: center;
      margin-bottom: 30px;
      picture {
        align-self: center;
      }
    }
  }
  &__img {
    max-width: 612px;
    width: 100%;
    height: 462px;
    @media (max-width: 1279px) {
      max-width: none;
      height: 371px;
      margin: 0 auto 24px;
    }
    @media (max-width: 767px) {
      width: 100%;
      height: auto;
      margin-bottom: 30px;
    }
    img {
      @include contain;
    }
  }
  &__list {
    counter-reset: num;
  }
  &__item {
    padding: 30px 30px 30px 105px;
    background-color: #F9F9F9;
    border-radius: 10px;
    position: relative;
    @media (max-width: 767px) {
      padding: 74px 16px 16px;
    }
    &::before {
      counter-increment: num;
      content: counter(num, decimal-leading-zero);
      position: absolute;
      top: 30px;
      left: 30px;
      @include h4-medium;
      color: rgba(#373737, .22);
      @media (max-width: 767px) {
        top: 16px;
        left: 16px;
      }
    }
    &:nth-child(3n+2) {
      background-color: #F0F6FF;
    }
    &:nth-child(3n+3) {
      background-color: #E2ECFF;
    }
    &:not(:last-child) {
      margin-bottom: 13px;
    }
    &-title {
      @include h4-medium;
      margin-bottom: 13px;
      @media (max-width: 767px) {
        letter-spacing: normal;
      }
    }
    &-description {
      @include body;
    }
  }
}