.invitation {
  margin: 80px 0;
  background-color: $blue-light;
  position: relative;
  z-index: 1;
  @media (max-width: 1279px) {
    margin: 50px 0;
  }
  @media (max-width: 767px) {
    margin: 30px 0;
  }
  &__img {
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    img {
      @include cover;
      @media (max-width: 1279px) {
        object-position: 38%;
      }
    }
  }
  &__content {
    min-height: 544px;
    padding: 93px 0;
    @media (max-width: 1279px) {
      padding: 30px 0;
    }
    @media (max-width: 767px) {
      padding: 16px 0;
    }
  }
  &__text {
    max-width: 600px;
  }
  &__title {
    @include h4-medium;
    margin-bottom: 13px;
    @media (max-width: 767px) {
      letter-spacing: normal;
    }
  }
  &__description {
    @include h5;
    margin-bottom: 24px;
    @media (max-width: 767px) {
      margin-bottom: 16px;
      br {
        display: none;
      }
    }
  }
  &__button {
    width: 233px;
    @media (max-width: 1279px) {
      width: 233px;
    }
    @media (max-width: 767px) {
      max-width: 222px;
    }
  }
}