.services {
  padding: 80px 0;
  @media (max-width: 1279px) {
    padding: 50px 0;
  }
  @media (max-width: 767px) {
    padding: 30px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      grid-template-columns: repeat(1, 1fr);
    }
  }
  &__item {
    min-height: 184px;
    display: flex;
    flex-direction: column;
    padding: 30px;
    background-color: $blue-light;
    transition: all $transition;
    @media (max-width: 1279px) {
      min-height: 180px;
    }
    @media (max-width: 767px) {
      min-height: auto;
      padding: 16px;
    }
    &:hover {
      background-color: $blue-dark;
      .services__item {
        &-title {
          color: $white;
        }
        &-arrow {
          transform: rotate(-45deg);
          svg {
            fill: $white;
          }
        }
        &-description {
          color: $white;
        }
      }
    }
    &:active {
      background-color: $blue-dark-active;
    }
    &-heading {
      display: flex;
      justify-content: space-between;
      align-items: center;
      gap: $gap;
      margin-bottom: 24px;
    }
    &-title {
      @include h3;
      transition: all $transition;
    }
    &-arrow {
      width: 24px;
      height: 24px;
      flex-shrink: 0;
      transition: all $transition;
      svg {
        width: 100%;
        height: 100%;
        fill: $grey;
        transition: all $transition;
      }
    }
    &-description {
      margin-top: auto;
      @include body;
      color: $grey;
      transition: all $transition;
    }
  }
  &__button.button {
    width: 100%;
    height: 100%;
    @include button;
    @media (max-width: 767px) {
      height: 57px;
    }
    &:before {
      content: none;
    }
  }
}