.support {
  margin: 80px 0;
  background-color: #D4E5FF;
  overflow: hidden;
  @media (max-width: 1279px) {
    margin: 50px 0;
  }
  @media (max-width: 767px) {
    margin: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(12, 1fr);
    }
    @media (max-width: 1279px) {
      display: flex;
      flex-direction: column;
      gap: 0;
    }
  }
  &__text {
    grid-column: span 2;
    padding: 80px 16px 80px 0;
    @media (max-width: 1600px) {
      grid-column: span 7;
      padding: 80px 0;
    }
    @media (max-width: 1279px) {
      padding: 40px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 0;
    }
  }
  &__title {
    @include h2-bold;
    margin-bottom: 40px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
    @media (max-width: 374px) {
      font-size: 24px;
    }
  }
  &__item {
    min-height: 143px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding: 24px 72px;
    background-color: $white;
    border-radius: 4px;
    clip-path: polygon(3.5% 0, 100% 0, 100% 80%, 96.5% 100%, 0 100%, 0 20%);
    @media (max-width: 1279px) {
      min-height: auto;
      padding: 24px;
    }
    @media (max-width: 767px) {
      min-height: 136px;
      padding: 16px;
      clip-path: polygon(6.5% 0, 100% 0, 100% 79%, 93.5% 100%, 0 100%, 0 19%);
    }
    &s {
      max-width: 763px;
      @media (max-width: 1279px) {
        max-width: none;
      }
    }
    &:not(:last-child) {
      margin-bottom: 16px;
    }
    &-title {
      @include h4-medium;
      color: $blue-mid;
      margin-bottom: 13px;
      @media (max-width: 767px) {
        letter-spacing: normal;
      }
      @media (max-width: 374px) {
        font-size: 21px;
      }
    }
    &-description {
      @include body;
    }
  }
  &__img {
    width: auto;
    height: 100%;
    margin-left: -16px;
    margin-right: -25vw;
    position: relative;
    @media (max-width: 1920px) {
      margin-right: -16vw;
    }
    @media (max-width: 1600px) {
      grid-column: span 5;
      margin-left: 0;
      margin-right: -9vw;
    }
    @media (max-width: 1440px) {
      margin-right: -60px;
    }
    @media (max-width: 1279px) {
      order: -1;
      height: 400px;
      margin: 0 -30px;
    }
    @media (max-width: 1023px) {
      height: 300px;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 1279px) {
        position: static;
      }
    }
    img {
      @include cover;
    }
  }
}