.feature {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 41px $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__title {
    grid-column: span 2;
    @include h2-bold;
    @include title-img;
    display: inline-block;
    @media (max-width: 1279px) {
      margin-bottom: 45px;
    }
    @media (max-width: 767px) {
      display: flex;
      flex-wrap: wrap;
      justify-content: flex-start;
      margin-bottom: 30px;
      span {
        order: +1;
        width: 100%;
      }
    }
    picture {
      margin-bottom: -5px;
      display: inline-block;
      @media (max-width: 767px) {
        margin-bottom: -15px;
      }
      &:not(:last-child) {
        margin-right: -13px;
        @media (max-width: 767px) {
          margin-right: -18px;
        }
      }
    }
    img {
      width: 48px;
      height: 48px;
      @media (max-width: 767px) {
        width: 36px;
        height: 36px;
      }
    }
  }
  &__img {
    grid-row: 1 / span 2;
    width: 100%;
    height: 100%;
    border-radius: 10px;
    overflow: hidden;
    position: relative;
    @media (max-width: 1279px) {
      height: 388px;
      margin-bottom: 16px;
    }
    @media (max-width: 1023px) {
      height: 288px;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 1279px) {
        position: static;
      }
    }
    img {
      @include cover;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 30px;
    background-color: #F9F9F9;
    border-radius: 10px;
    @media (max-width: 1279px) {
      padding: 16px;
      justify-content: flex-start;
    }
    &s {
      grid-column: span 2;
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &:last-child {
      grid-column: span 2;
      background-color: #E2FFE5;
      @media (max-width: 1279px) {
        padding: 30px;
      }
      @media (max-width: 767px) {
        grid-column: span 1;
        padding: 16px;
      }
      .feature__item-title {
        color: #00780C;
      }
    }
    &-title {
      @include h4;
      margin-bottom: 13px;
      @media (max-width: 1279px) {
        padding-right: 5px;
      }
      @media (max-width: 767px) {
        padding-right: 0;
        letter-spacing: normal;
      }
    }
    &-description {
      @include body;
    }
  }
}