.payment {
  margin: 80px 0;
  background-color: #F0F6FF;
  overflow: hidden;
  @media (max-width: 1279px) {
    margin: 50px 0;
  }
  @media (max-width: 767px) {
    margin: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: flex;
      flex-direction: column;
      gap: 0;
    }
  }
  &__heading {
    padding: 80px 0;
    grid-column: span 7;
    @media (max-width: 1279px) {
      padding: 60px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 0;
    }
  }
  &__text {
    margin-bottom: 40px;
    @media (max-width: 767px) {
      margin-bottom: 30px;
    }
  }
  &__title {
    @include h2-bold;
    margin-bottom: 10px;
  }
  &__subtitle {
    @include h4;
  }
  &__item {
    min-height: 267px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 20px 32px;
    background-color: $white;
    border-radius: 10px;
    text-align: center;
    @media (max-width: 767px) {
      min-height: 180px;
    }
    @media (max-width: 374px) {
      padding: 20px 16px;
    }
    &:last-child {
      background-color: #3586FF;
      color: $white;
    }
    &s {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $gap;
      @media (max-width: 767px) {
        grid-template-columns: repeat(1, 1fr);
      }
    }
    &-heading {
      display: flex;
      justify-content: center;
      flex-wrap: wrap;
      gap: 12px;
      margin-bottom: 16px;
    }
    &-img {
      width: 32px;
      height: 32px;
      flex-shrink: 0;
      img {
        @include contain;
      }
    }
    &-title {
      @include h4;
      margin-bottom: 19px;
      @media (max-width: 767px) {
        font-size: 32px;
      }
    }
    &-description {
      @include body;
      @media (max-width: 767px) {
        font-size: 20px;
      }
    }
  }
  &__img {
    grid-column: 9 / span 4;
    width: auto;
    height: 100%;
    margin-left: -16px;
    margin-right: -25vw;
    position: relative;
    @media (max-width: 1920px) {
      margin-right: -16vw;
    }
    @media (max-width: 1600px) {
      margin-left: 0;
      margin-right: -9vw;
      grid-column: 8 / span 5;
    }
    @media (max-width: 1440px) {
      margin-right: -60px;
    }
    @media (max-width: 1279px) {
      order: -1;
      height: 480px;
      margin: 0 -30px;
    }
    @media (max-width: 1023px) {
      height: 380px;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 1279px) {
        position: static;
      }
    }
    img {
      @include cover;
    }
  }
}