.technology {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__title {
    padding-top: 23px;
    @include h2-bold;
    text-align: center;
    margin-bottom: 29px;
    @media (max-width: 1279px) {
      padding-top: 0;
      padding-left: 16px;
      text-align: left;
      margin-bottom: 41px;
      .decor {
        margin-left: -16px;
        // margin-bottom: -4px;
      }
    }
    @media (max-width: 767px) {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-top: 20px;
      padding-left: 0;
      text-align: center;
      margin-bottom: 29px;
      .decor {
        width: min-content;
        margin-left: 0;
      }
    }
  }
  &__body {
    display: grid;
    grid-template-columns: repeat(12, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      display: block;
    }
  }
  &__content {
    grid-column: 2 / 12;
  }
  &__item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: $gap;
    @media (max-width: 767px) {
      align-items: flex-start;
    }
    &:not(:last-child) {
      margin-bottom: -48px;
      @media (max-width: 1279px) {
        margin-bottom: 30px;
      }
    }
    &:nth-child(2) {
      .technology__item {
        &-circle {
          margin-left: 110px;
          background-color: #F0FFF2;
          @media (max-width: 1279px) {
            margin: 0;
          }
        }
      }
    }
    &:nth-child(3) {
      .technology__item {
        &-circle {
          margin-left: 220px;
          background-color: #AACCFF;
          @media (max-width: 1279px) {
            margin: 0;
          }
        }
      }
    }
    &-circle {
      width: 207px;
      height: 207px;
      border-radius: 50%;
      flex-shrink: 0;
      background-color: #F0F6FF;
      display: flex;
      justify-content: center;
      align-items: center;
      @media (max-width: 1279px) {
        width: 165px;
        height: 165px;
      }
      @media (max-width: 767px) {
        width: 74px;
        height: 74px;
      }
    }
    &-img {
      width: 60px;
      height: 60px;
      @media (max-width: 767px) {
        width: 36px;
        height: 36px;
      }
      img {
        @include contain;
      }
    }
    &-arrow {
      flex-grow: 1;
      display: flex;
      justify-content: flex-end;
      overflow: hidden;
      height: 16px;
      @media (max-width: 767px) {
        display: none;
      }
      svg {
        flex-shrink: 0;
        width: 431px;
        height: 100%;
      }
    }
    &-text {
      flex-shrink: 0;
      width: 100%;
      max-width: 430px;
      padding: 20px 0;
      @media (max-width: 1279px) {
        max-width: 436px;
        padding: 0;
      }
      @media (max-width: 767px) {
        flex-shrink: 1;
        max-width: none;
        width: auto;
      }
    }
    &-title {
      @include h4-medium;
      margin-bottom: 13px;
      @media (max-width: 767px) {
        letter-spacing: normal;
      }
    }
    &-description {
      @include body;
    }
  }
}