@mixin cover {
  height: 100%;
  object-fit: cover;
}

@mixin contain {
  height: 100%;
  object-fit: contain;
}

@mixin ttcom {
  font-family: 'TT Commons', sans-serif;
}

@mixin hbreit {
  font-family: 'Halvar Breitschrift', sans-serif;
}

@mixin section {
  padding: 80px 0;
  @media (max-width: 1279px) {
    padding: 50px 0;
  }
  @media (max-width: 767px) {
    padding: 30px 0;
  }
}

@mixin title-img {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 24px;
  picture {
    align-self: flex-start;
  }
  img {
    width: 60px;
    height: 60px;
    @media (max-width: 1279px) {
      width: 40px;
      height: 40px;
    }
  }
}

@mixin h1 {
  @include hbreit;
  font-size: 70px;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -.02em;
  text-transform: uppercase;
  @media (max-width: 1279px) {
    font-size: 52px;
  }
  @media (max-width: 767px) {
    font-size: 42px;
  }
  @media (max-width: 374px) {
    font-size: 38px;
  }
}

@mixin h2 {
  @include hbreit;
  font-size: 54px;
  line-height: 1.1;
  letter-spacing: -.02em;
  @media (max-width: 1279px) {
    font-size: 36px;
  }
  @media (max-width: 767px) {
    font-size: 27px;
  }
}

@mixin h2-bold {
  @include hbreit;
  font-size: 50px;
  font-weight: 700;
  line-height: 1.1;
  letter-spacing: -.02em;
  text-transform: uppercase;
  @media (max-width: 1279px) {
    font-size: 40px;
  }
  @media (max-width: 767px) {
    font-size: 28px;
  }
}

@mixin h3 {
  font-size: 28px;
  font-weight: 500;
  letter-spacing: -.02em;
  @media (max-width: 1279px) {
    font-size: 25px;
  }
  @media (max-width: 767px) {
    font-size: 22px;
  }
}

@mixin h4 {
  @include hbreit;
  font-size: 32px;
  line-height: 1.1;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 24px;
  }
}

@mixin h4-medium {
  @include hbreit;
  font-size: 32px;
  font-weight: 500;
  line-height: 1.1;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 24px;
  }
}

@mixin h5 {
  font-size: 24px;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 18px;
  }
}

@mixin h5-medium {
  font-size: 24px;
  font-weight: 500;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 18px;
  }
}

@mixin body {
  font-size: 20px;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 16px;
  }
}

@mixin body-medium {
  font-size: 20px;
  font-weight: 500;
  line-height: 1.1;
  @media (max-width: 767px) {
    font-size: 16px;
  }
}

@mixin body-medium-small {
  font-size: 17px;
  font-weight: 500;
  letter-spacing: -.02em;
  @media (max-width: 767px) {
    font-size: 14px;
  }
}

@mixin button {
  @include hbreit;
  font-size: 20px;
  font-weight: 700;
  line-height: 1;
  @media (max-width: 767px) {
    font-size: 17px;
  }
}

@mixin button-small {
  @include hbreit;
  font-size: 15px;
  font-weight: 700;
  line-height: 1;
}

