.service-card {
  // height: 989px;
  min-height: calc(100vh - 87px);
  padding: 30px 90px;
  background-color: $white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 60px;
  @media (max-width: 1279px) {
    min-height: auto;
    padding: 70px 30px;
    gap: 40px;
  }
  @media (max-width: 767px) {
    padding: 70px 16px;
  }
  &:nth-child(odd) {
    .service-card {
      &__button {
        &::before {
          background-color: $dark;
        }
        &:hover {
          color: $white;
          svg {
            fill: $white;
          }
        }
      }
    }
  }
  &:nth-child(even) {
    background-color: $dark;
    color: $white;
    .service-card {
      &__category {
        color: $white;
      }
    }
  }
  &__title {
    @include h4;
  }
  &__category{
    @include h3;
    color: $blue-dark;
    margin-bottom: 16px;
  }
  &__brand {
    width: auto;
    height: 46px;
    img {
      @include contain;
    }
    &s {
      display: flex;
      flex-wrap: wrap;
      gap: 12px;
    }
  }
  &__button {
    flex-shrink: 0;
    @media (max-width: 1279px) {
      width: 346px;
    }
  }
}