.result {
  @include section;
  @media (max-width: 767px) {
    padding: 40px 0;
  }
  &__title {
    @include h2-bold;
    @include title-img;
    gap: 16px;
    justify-content: flex-start;
    margin-bottom: 38px;
    @media (max-width: 1279px) {
      flex-wrap: wrap;
    }
    @media (max-width: 767px) {
      row-gap: 8px;
      margin-bottom: 30px;
    }
    @media (max-width: 374px) {
      font-size: 24px;
    }
    span {
      color: $blue-mid;
    }
  }
  table {
    width: 100%;
    border-collapse: collapse;
    thead {
      tr {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
      }
      th {
        padding: 20px 30px;
        background-color: #F0F1F1;
        border-top-left-radius: 16px;
        @include h4-medium;
        font-size: 22px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: left;
        @media (max-width: 1279px) {
          font-size: 18px;
        }
        @media (max-width: 767px) {
          padding: 20px 6px;
          text-align: center;
          font-size: 13px;
        }
        &:last-child {
          background-color: #D5E6FF;
          border-top-left-radius: 0;
          border-top-right-radius: 16px;
          color: $blue-mid;
        }
      }
    }
    tbody {
      tr {
        display: grid;
        grid-template-columns: 60px 1fr 1fr;
        @media (max-width: 1279px) {
          grid-template-columns: 60px 1fr;
        }
        &:last-child {
          td {
            &:first-child {
              border-bottom-left-radius: 16px;
            }
            &:last-child {
              border-bottom-right-radius: 16px;
            }
          }
        }
      }
      td {
        display: flex;
        align-items: center;
        min-height: 67px;
        padding: 12px 30px;
        @include body;
        border: 1px solid #F0F1F1;
        @media (max-width: 1279px) {
          min-height: auto;
          padding: 16px;
        }
        @media (max-width: 767px) {
          padding: 12px 15px;
        }
        &:first-child {
          padding: 5px;
          justify-content: center;
          color: #B3B3B3;
          @media (max-width: 1279px) {
            grid-row: span 2;
          }
          @media (max-width: 767px) {
            font-size: 18px;
          }
        }
        &:nth-child(2) {
          margin-right: 30px;
          @media (max-width: 1279px) {
            margin: 0;
          }
        }
        &:last-child {
          background-color: #F0F6FF;
          border-color: #DEE9FF;
          margin-left: -30px;
          @media (max-width: 1279px) {
            margin: 0;
          }
        }
      }
    }
  }
}