.partners {
  padding: 60px 0;
  background-color: $dark;
  color: $white;
  @media (max-width: 767px) {
    padding: 30px 0;
  }
  &__heading {
    display: flex;
    align-items: flex-end;
    gap: 4px;
    margin-bottom: 30px;
    @media (max-width: 1279px) {
      gap: 8px;
    }
  }
  &__title {
    @include h2;
    @media (max-width: 767px) {
      br {
        display: none;
      }
      span {
        position: relative;
        padding-right: 28px;
        &::after {
          content: '';
          display: inline-block;
          position: absolute;
          right: 0;
          top: 55%;
          transform: translateY(-50%);
          width: 24px;
          height: 24px;
          background: url(../img/content/arrow-down.svg) 50% / contain no-repeat;
        }
      }
    }
  }
  &__arrow {
    width: 74px;
    height: 74px;
    flex-shrink: 0;
    @media (max-width: 1279px) {
      width: 46px;
      height: 46px;
    }
    @media (max-width: 767px) {
      display: none;
    }
    svg {
      width: 100%;
      height: 100%;
      fill: $blue-dark;
      transform: rotate(90deg);
    }
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: $gap;
    @media (max-width: 1279px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 767px) {
      gap: 15px;
    }
  }
  &__item {
    width: 100%;
    height: 200px;
    border: 1px solid $grey-20;
    overflow: hidden;
    @media (max-width: 767px) {
      height: 110px;
    }
    svg {
      width: 100%;
      height: 100%;
      @media (max-width: 767px) {
        transform: scale(1.4);
        pointer-events: none;
      }
    }
  }
}