.accounting {
  margin: 80px 0;
  background-color: $grey-light;
  overflow: hidden;
  @media (max-width: 1279px) {
    margin: 50px 0;
  }
  @media (max-width: 767px) {
    margin: 40px 0;
  }
  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: $gap;
    @media (max-width: 1600px) {
      grid-template-columns: repeat(2, 1fr);
    }
    @media (max-width: 1279px) {
      display: flex;
      flex-direction: column;
      gap: 0;
    }
  }
  &__text {
    grid-column: span 2;
    padding: 80px 16px 80px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @media (max-width: 1600px) {
      padding: 70px 0;
      grid-column: span 1;
    }
    @media (max-width: 1279px) {
      padding: 60px 0;
    }
    @media (max-width: 767px) {
      padding: 30px 0;
    }
  }
  &__title {
    @include h2-bold;
    margin-bottom: 40px;
  }
  &__list {
    max-width: 650px;
    li {
      &::before {
        border-color: $white;
      }
    }
  }
  &__img {
    width: auto;
    min-height: 700px;
    height: 100%;
    margin-left: -16px;
    margin-right: -25vw;
    position: relative;
    @media (max-width: 1920px) {
      margin-right: -16vw;
    }
    @media (max-width: 1600px) {
      margin-left: 0;
      margin-right: -9vw;
    }
    @media (max-width: 1440px) {
      margin-right: -60px;
    }
    @media (max-width: 1279px) {
      order: -1;
      min-height: auto;
      height: 480px;
      margin: 0 -30px;
    }
    @media (max-width: 1023px) {
      height: 380px;
    }
    picture {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      @media (max-width: 1279px) {
        position: static;
      }
    }
    img {
      @include cover;
    }
  }
}